.slide {
  width: 23%;
  margin-top: -1px;
  box-shadow: 1px 1px 4px gray;
  position: absolute;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  z-index: 2;
  background-color: white;
}
.same {
  margin-top: 20px;
  margin-left: 17px;
  font-size: 14px;
  font-family: "Nunito", sans-serif;
  color: #4682b4;
  font-weight: 700;
  cursor: pointer;
}

.same::first-letter {
  color: orangered;
  font-size: 22px;
  margin-right: 7px;
  font-weight: 900;
}

.same:hover {
  color: black;
}
.line {
  width: 100% !important;
  margin-top: 20px;
  color: gray;
  opacity: 0.3;
}
.slider{
  position: absolute;
  display: none;

}