.text {
  font-size: 9px;
  padding: 15px 10px 15px 15px;
  text-align: start;
  color: black;
  border: none;
  cursor: pointer;
}

.table-container {
  width: 100%;
  background-color: #ebecf0;

  margin-top: 10px;
 
  display: flex;
  flex-direction: column;
  align-items: center;
}
.table {
  background-color: rgb(255, 255, 255);
  border-radius: 5px;
  box-shadow: 1px 1px 4px gray;
  border: 1px solid #989898;
  border-collapse: collapse;
  table-layout: fixed;
  width: 67%;
  min-width: 350px;
}
.curr {
  text-align: start;
  color: #575757;
  font-size: 12px !important;
  font-weight: 600;
}
tr td {
  border: 1px solid #989898;
  padding: 13px 10px;
  font-size: 12px;
  overflow: hidden;
}
th {
  border: 1px solid #989898;
  color: #a9a9a9;
  font-size: 12px;
  padding: 7px 19px 7px 19px;
  font-weight: 600;
  overflow: hidden;
}
.status {
  color: #3cb371;
  font-weight: 650;
  overflow: hidden;
}
