.header-container {
  padding: 10px;
  box-shadow: 1px 1px 7px gray;
  display: grid;
  grid-template-columns: 1fr 1fr;
  position: relative;
  z-index: 1;
}
.three-line {
  margin: 5px;
}
.logo {
  font-size: 30px;
  letter-spacing: 3px;
  margin-left: 10px;
  /* font-weight: bold; */
}
.logo::first-letter {
  font-size: 40px;
}
.dhan {
  color: orangered;
}
.left {
  display: flex;
}
.right {
  display: flex;
  justify-content: flex-end;
  flex-direction: row;
  align-items: center;
}
.search {
  outline: none;
  border: 1px solid lightgrey;
  height: 20px;
  padding: 4px;
  border-radius: 5px;
  margin-right: 7px;
}
.btn {
  padding: 10px 17px;
  margin-right: 10px;
  border: none;
  background-color: orangered;
  color: white;
  text-shadow: 1px 1px 1px black;
  border-radius: 7px;
  cursor: pointer;
}
.search:focus {
  border: 1px solid lightskyblue;
}
.btn:hover {
  box-shadow: 1px 1px 1px lightgray;
}
